.table {
    margin-top: 20px;
    overflow: scroll;
    height: 25rem;
    color: #6a5d5d;
  }
  
  .table tr {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 3px;
  }
  
  .table td {
    padding: 0.4rem;
    display: flex;
    justify-content: center;
    gap: 10px;
    align-items: center;
  }
  
  .table td img {
    height: 30px;
    width: 30px;
    border-radius: 50%;
  }
  
  .table tr:nth-of-type(odd) {
    background-color: #495464;
    color: #fff;
  }
  